h2 {
  font-size: 2.5rem;
  line-height: 120%;
  margin: 0;
  .text_color {
    color: rgb(111, 155, 164);
  }
}
.agreement-check {
  input {
    z-index: 2;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
    }
  }
  label {
    font-size: 0.75rem;
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0.0625rem;
      left: 0;
      width: 0.875rem;
      height: 0.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      border: 0.0625rem solid rgb(255, 255, 255);
      border-radius: 0.3125rem;
    }
    a {
      position: relative;
      z-index: 3;
      &:hover {
        color: rgb(229, 225, 218);
      }
    }
  }
}
.slick-arrow {
  position: static;
  width: 3.75rem;
  height: auto;
  background: transparent;
  box-shadow: unset !important;
  &:hover {
    img:nth-child(1) {
      display: none;
    }
    img:nth-child(2) {
      display: block;
    }
  }
  img {
    width: 100%;
    height: auto;
  }
  img:nth-child(2) {
    display: none;
  }
}
.left-anim {
  transform: translate(-60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.left-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.right-anim {
  transform: translate(60%, 0);
  opacity: 0;
  transition: all 0.6s;
}
.right-anim.active {
  transform: translate(0,0);
  opacity: 1;
}
.with-fancybox {
  .characteristic {
    position: relative;
    left: unset;
    top: unset;
    transform: unset;
    opacity: 1;
    pointer-events: auto;
  }
}
.fancybox__backdrop {
  background: rgba(111, 155, 164, 0.5);
  backdrop-filter: blur(1.25rem);
}
.navigation {
  padding: 3.125rem;
  &__list {
    display: inline-grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    font-size: 1.5rem;
    line-height: 130%;
    a:hover {
      opacity: 0.7;
    }
  }
}
.fancybox__content {
  border-radius: 1.25rem;
  padding: 0;
  .form {
    background: rgb(37, 40, 42);
    backdrop-filter: blur(1.25rem);
    border-radius: 1.25rem;
    padding: 3.125rem;
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        width: 80%;
        height: 0.125rem;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 1) 49%, rgba(0, 0, 0, 0.1) 100%);
        pointer-events: none;
    }
    &:before {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: 80%;
        height: 0.125rem;
        background: linear-gradient(90deg, rgba(0, 0, 0, 0.1) 0%, rgba(255, 255, 255, 1) 49%, rgba(0, 0, 0, 0.1) 100%);
        pointer-events: none;
    }
  }
  .form_block {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.875rem;
  }
}



@media screen and (max-width: 1600px) {
  h2 {
    font-size: 2.25rem;
  }
}

@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2rem;
  }
}

@media screen and (max-width: 1023px) {
  .btn-golden {
    span {
      font-size: 0.875rem;
    }
  }
  h2 {
    font-size: 1.75rem;
  }
}

@media screen and (max-width: 576px) {
  h2 {
    font-size: 1.5rem;
  }
}