.main-section {
  background-image: url("../img/mainn-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #ffffff;
  padding: 10rem 0;
  .container-fluid {
    z-index: 3;
  }
  &__inner {
    position: relative;
    max-width: 52.8125rem;
    margin: 0 auto;
  }
  &__wrap {
    p {
      max-width: 21.875rem;
      font-size: 1.375rem;
      line-height: 130%;
      font-weight: 500;
      margin: 1.25rem 0 0 0;
      strong {
        font-weight: 700;
      }
    }
  }
  h1 {
    font-size: 3rem;
    line-height: 130%;
    font-weight: 600;
    margin: 0;
  }
  &__group {
    position: relative;
    max-width: 31.625rem;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(1.25rem);
    border-radius: 1.25rem 0 0 1.25rem;
    padding: 3rem 5.625rem;
    margin: 7.1875rem 0 0 4.5rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 80%;
      height: 0.125rem;
      background: linear-gradient(90deg, rgba(0,0,0,0.1) 0%, rgba(255,255,255,1) 49%, rgba(0,0,0,0.1) 100%);
      pointer-events: none;
    }
    &:before {
      content: '';
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 80%;
      height: 0.125rem;
      background: linear-gradient(90deg, rgba(0,0,0,0.1) 0%, rgba(255,255,255,1) 49%, rgba(0,0,0,0.1) 100%);
      pointer-events: none;
    }
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
  }
  &__btn {
    margin-top: 1.5rem;
    span {
      padding: 1.25rem 5rem;
    }
  }
  &__wrap2 {
    position: relative;
    width: 100%;
    margin: 11.875rem 0 0 4.5rem;
  }
  &__items {
    position: relative;
    z-index: 1;
    display: grid;
    grid-template-columns: repeat(2, calc(50%));
    grid-auto-rows: 1fr;
    background: rgb(255, 255, 255);
    backdrop-filter: blur(1.25rem);
    border-radius: 1.25rem;
    &:before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 100%;
      height: 0.03125rem;
      background: #6F9BA4;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: 0.03125rem;
      height: 100%;
      background: #6F9BA4;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1.875rem 2.5rem;
    &-icon {
      width: 3.75rem;
      flex-shrink: 0;
    }
    &-text {
      max-width: 17.375rem;
      font-size: 1.125rem;
      line-height: 130%;
      font-weight: 600;
      color: rgb(37, 40, 42);
    }
  }
  &__img {
    position: absolute;
    z-index: 2;
    top: 10rem;
    left: 0;
    max-width: 33.125rem;
    img {
      width: 100%;
    }
    img:nth-child(1) {
      position: relative;
      z-index: 1;
    }
    img:nth-child(2) {
      position: absolute;
      top: 17rem;
      left: calc(100% - 10rem);
      max-width: 15.75rem;
      opacity: 0.15;
    }
  }
  &__img2 {
    position: absolute;
    z-index: 2;
    left: calc(100% - 10rem);
    bottom: -10rem;
    max-width: 27.3125rem;
    width: 100%;
    img {
      width: 100%;
      max-width: unset;
      max-height: unset;
    }
  }
  &__img3 {
    position: absolute;
    top: -1rem;
    left: calc(100% - 2.2rem);
    width: 13.125rem;
  }
  &__img4 {
    position: absolute;
    right: calc(100% - 4rem);
    bottom: 0;
    width: 16.875rem;
    img {
      width: 100%;
      max-width: unset;
      max-height: unset;
    }
  }
  .ellipses {
    span:nth-child(1) {
      position: absolute;
      top: -21.875rem;
      left: 6.25rem;
      width: 73.625rem;
      height: 73.625rem;
      background: radial-gradient(53.08% 53.08% at 53% 47%,rgb(13, 82, 96),rgba(18, 68, 80, 0) 100%);
      filter: blur(3.125rem);
      border-radius: 50%;
      pointer-events: none;
    }
    span:nth-child(2) {
      position: absolute;
      top: 7rem;
      left: -29.375rem;
      width: 54.375rem;
      height: 54.375rem;
      background: radial-gradient(53.08% 53.08% at 53% 47%,rgb(13, 82, 96),rgba(18, 68, 80, 0) 100%);
      filter: blur(3.125rem);
      border-radius: 50%;
      pointer-events: none;
      opacity: 0;
    }
    span:nth-child(3) {
      position: absolute;
      top: 7rem;
      right: -29.375rem;
      width: 54.375rem;
      height: 54.375rem;
      background: radial-gradient(53.08% 53.08% at 53% 47%,rgb(13, 82, 96),rgba(18, 68, 80, 0) 100%);
      filter: blur(3.125rem);
      border-radius: 50%;
      pointer-events: none;
    }
  }
}
.services {
  color: #ffffff;
  background: rgb(37, 40, 42);
  border-top: 0.0625rem solid rgb(255, 255, 255);
  border-bottom: 0.0625rem solid rgb(255, 255, 255);
  padding: 0;
  .container-fluid {
    padding: 0;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25%));
  }
  &__group {
    margin-top: 2rem;
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
  }
  &__btn {
    margin-top: 1.25rem;
    span {
      padding: 1.375rem 4.375rem;
    }
  }
}
.service-card {
  position: relative;
  border-right: 0.0625rem solid rgb(255, 255, 255);
  padding: 5rem 2.5rem;
  &:nth-child(1),
  &:nth-child(3) {
    &:after {
      content: '';
      position: absolute;
      bottom: 100%;
      right: 0;
      width: 0.0625rem;
      height: 625rem;
      background: rgba(255, 255, 255, 0.2);
    }
  }
  &__desc {
    position: relative;
    z-index: 1;
    max-width: 18.125rem;
  }
  &__title {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
    margin-top: 0.9375rem;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 19.375rem;
    width: 100%;
    img {
      width: 100%;
    }
  }
}
.catalog {
  z-index: 2;
  padding: 6.25rem 0 3.125rem;
  h2 {
    text-align: center;
  }
  &__tabs-head {
    display: flex;
    justify-content: center;
    align-items: stretch;
    gap: 1.25rem;
    margin-top: 3.125rem;
  }
  &__tab {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background: transparent;
    border: 0.0625rem solid rgb(37, 40, 42);
    border-radius: 0.625rem;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: rgb(37, 40, 42);
      span {
        color: #ffffff;
      }
    }
    span {
      min-width: unset;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 600;
      color: rgb(37, 40, 42);
      padding: 1.125rem 1.875rem;
    }
  }
  .tab--active {
    background: rgb(37, 40, 42);
    span {
      color: #ffffff;
    }
  }
  &__tabs-body {
    margin-top: 2rem;
  }
  .tab__content {
    display: none;
  }
  .tab__content--active {
    display: block;
  }
  &__cards {
    display: grid;
    grid-template-columns: repeat(4, calc(25% - 5.25rem / 4));
    gap: 1.875rem;
  }
}
.catalog-card {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  box-shadow: 0.625rem 0.625rem 0.3125rem 0 rgba(255,255,255,0.75);
  cursor: pointer;
  &:hover {
    .catalog-card__icon {
      opacity: 1;
      pointer-events: auto;
    }
    .catalog-card__hover {
      opacity: 1;
      pointer-events: auto;
    }
  }
  &__top {
    position: relative;
  }
  &__img {
    position: relative;
    padding-bottom: 126%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 5.125rem;
    height: 5.125rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s;
    img {
      width: 2.9375rem;
    }
  }
  &__desc {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    flex-grow: 1;
    background: #ffffff;
    padding: 1.75rem;
  }
  &__title {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 600;
    color: rgb(37, 40, 42);
  }
  &__price {
    font-size: 1.125rem;
    line-height: 130%;
    font-weight: 500;
    color: rgb(37, 40, 42);
    margin-top: 2rem;
    margin-bottom: auto;
  }
  &__hover {
    position: absolute;
    z-index: 1;
    top: 100%;
    left: 0;
    width: 100%;
    background: #fff;
    padding-bottom: 1.875rem;
    opacity: 0;
    pointer-events: none;
    transition: all 0.2s;
  }
  &__btn {
    display: inline-flex !important;
    gap: 0.5rem;
    padding-right: 4.375rem !important;
    span {
      padding: 1.125rem 0 1.125rem 4.375rem;
    }
    i {
      position: relative;
      z-index: 2;
      width: 1.25rem;
      flex-shrink: 0;
      img {
        width: 100%;
      }
    }
  }
}
.characteristic {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 94.5rem;
  width: 100%;
  background: transparent;
  border-radius: 1.25rem;
  padding: 0;
  margin: 3rem 0;
  opacity: 0;
  pointer-events: none;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
  &__left {
    position: relative;
    width: 45%;
  }
  &__right {
    position: relative;
    width: 55%;
  }
  &__slider {
    position: absolute;
    top: -0.0625rem;
    left: 0;
    width: 100%;
    height: calc(100% + 0.125rem);
    overflow: hidden;
  }
  &__slider-wrap {
    height: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__slider-small {
    height: 100%;
    width: 10.625rem;
    flex-shrink: 0;
    border: 0.0625rem solid rgb(37, 40, 42);
    border-radius: 1.25rem 0 0 1.25rem;
    overflow: hidden;
    .slick-arrow {
      display: none !important;
    }
    .slick-list {
      border-radius: 1.25rem 0 0 1.25rem;
    }
    .slick-track {
      border-radius: 1.25rem 0 0 1.25rem;
    }
    .slick-slide {
      height: calc(100% / 3) !important;
    }
    .slick-slide:not(:last-child) {
      border: none;
      border-bottom: 0.0625rem solid rgb(37, 40, 42) !important;
    }
  }
  &__slider-big {
    height: 100%;
    width: calc(100% - 10.625rem);
    border: 0.0625rem solid rgb(37, 40, 42);
    border-left: unset;
    border-right: unset;
  }
  .slick-list {
    height: 100% !important;
    padding: 0 !important;
  }
  .slick-track {
    height: 100% !important;
  }
  .slick-slide {
    div {
      height: 100%;
    }
  }
  .slick-vertical .slick-slide {
    border: none;
  }
  &__slide {
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__slider-nav {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: calc(100% - 10.625rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
  }
  .slick-arrow {
    width: 3.75rem;
    &:hover {
      img:nth-child(1) {
        display: none;
      }
      img:nth-child(2) {
        display: block;
      }
    }
    img {
      width: 100%;
    }
    img:nth-child(2) {
      display: none;
    }
  }
  &__prev {
    transform: rotate(-180deg);
  }
  &__slider-desc {
    position: absolute;
    right: 0;
    bottom: 0.625rem;
    width: calc(100% - 10.625rem);
    padding-left: 0.625rem;
    p {
      max-width: 16.6875rem;
      background: rgb(255, 255, 255);
      padding: 1.25rem;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 500;
      color: rgb(37, 40, 42);
      margin: 0;
    }
    a {
      text-decoration: underline;
      &:hover {
        opacity: 0.7;
      }
    }
  }
  @media screen and (min-width: 1701px) {
    .f-button {
      top: 0 !important;
      right: -3rem !important;
    }
  }
  .f-button {
    width: 1.75rem;
    svg {
      width: 100%;
      height: auto;
    }
  }
  &__desc-top {
    position: relative;
    display: flex;
    justify-content: space-between;
    gap: 2rem;
    background-color: #ffffff;
    border-radius: 0 1.25rem 0 0;
    color: rgb(37, 40, 42);
    padding: 3.125rem 3.125rem 1.875rem 3.125rem;
    ul {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.625rem;
      margin-top: 2rem;
    }
    ul li {
      display: flex;
      justify-content: space-between;
      gap: 0.625rem;
    }
    ul li span {
      font-size: 1rem;
      line-height: 130%;
    }
    ul li span:nth-child(2) {
      text-align: right;
    }
  }
  &__group {
    position: relative;
    z-index: 1;
    width: 100%;
    &:nth-child(1) {
      max-width: 25rem;
    }
    &:nth-child(2) {
      max-width: 13.75rem;
    }
  }
  &__title {
    font-family: PlayfairDisplay;
    font-size: 1.875rem;
    line-height: 130%;
  }
  &__info {
    font-size: 0.875rem;
    line-height: 130%;
    font-weight: 500;
    margin-top: 2rem;
    a {
      text-decoration: underline;
    }
    a:hover {
      text-decoration: unset;
    }
  }
  &__price {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
    font-size: 1rem;
    line-height: 130%;
  }
  &__btns {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625rem;
    margin-top: 2rem;
  }
  .catalog-card__btn:nth-child(1) {
    justify-content: center;
    background: rgb(229, 225, 218);
    border: 0.0625rem solid rgba(0, 0, 0, 0.2);
    border-radius: 1.25rem;
    &:hover {
      background: rgb(206, 205, 203);
    }
    span {
      min-width: unset;
      font-size: 1rem;
      line-height: 130%;
    }
  }
  .catalog-card__btn {
    width: 100%;
    padding-right: 0.5rem !important;
    margin: 0;
    span {
      font-weight: 500;
      padding-left: 0.5rem;
    }
  }
  &__desc-bottom {
    position: relative;
    background: rgba(37, 40, 42, 0.7);
    border-radius: 0 0 1.25rem 0;
    color: #ffffff;
    padding: 1.875rem 3.125rem 3.125rem 3.125rem;
    ul {
      position: relative;
      z-index: 1;
      display: grid;
      grid-template-columns: 100%;
      gap: 2rem;
    }
    ul li {
      font-size: 1rem;
      line-height: 130%;
    }
    ul li strong {
      display: block;
      font-weight: 700;
      margin-bottom: 0.625rem;
    }
    .filter-bg {
      opacity: 0.1;
      img {
        border-radius: 0 0 1.25rem 0;
        opacity: 0.5;
      }
    }
  }
  .filter-bg {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
