  }
  .slick-list {
    overflow: unset;
    margin: 0 -0.9375rem;
  }
  .slick-track {
    display: flex;
    align-items: stretch;
  }
  .slick-slide {
    height: auto;
    margin: 0 0.9375rem;
    opacity: 0;
    pointer-events: none;
    & > div {
      height: 100%;
