#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  padding: 1rem 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.5rem;
  }
  .header__left {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }
  .header__right {
    display: flex;
    align-items: center;
    gap: 11.25rem;
  }
  .header__logo {
    width: 11.4375rem;
    img {
      width: 100%;
    }
  }
  .header__location {
    display: flex;
    gap: 1.125rem;
    &:hover {
      color: rgb(229, 225, 218);
    }
    i {
      width: 1rem;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    p {
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      display: block;
      font-weight: 600;
    }
  }
  .header__wrap {
    flex-shrink: 0;
    display: flex;
    gap: 2.5rem;
  }
  .header__socials {
    display: flex;
    gap: 1rem;
    .header__location {
      display: none;
    }
  }
  .header__social {
    max-width: 2rem;
    &:hover {
      opacity: 0.8;
    }
  }
  .header__phone {
    display: block;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(229, 225, 218) !important;
    &:hover {
      color: rgb(255, 255, 255) !important;
    }
  }
  .header__btn {
    background: transparent;
    &:hover {
      span {
        color: rgb(229, 225, 218) !important;
        border-color: rgb(229, 225, 218);
      }
    }
    span {
      min-width: unset;
      padding: 0;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 600;
      color: #ffffff;
      border-bottom: 0.0625rem dashed #ffffff;
    }
  }
}

@media screen and (max-width: 1600px) {
  #header {
    .header__right {
      gap: 2.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .header__inner {
      gap: 1.5rem;
    }
    .header__left {
      gap: 1.5rem;
    }
    .header__right {
      gap: 1.5rem;
    }
    .header__logo {
      width: 10rem;
    }
    .header__wrap {
      flex-direction: column;
      gap: 0.625rem;
    }
  }
}

@media screen and (max-width: 768px) {
  #header {
    .header__logo {
      width: 9rem;
    }
    .header__text {
      display: none;
    }
    .header__wrap {
      flex-direction: row;
      align-items: center;
      gap: 2rem;
    }
    .header__location {
      display: none;
    }
    .header__wrap {
      align-items: flex-end;
    }
    .header__socials {
      .header__location {
        width: 2rem;
        height: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgb(37, 40, 42);
        border-radius: 50%;
        i {
          display: flex;
          justify-content: center;
          align-items: center;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  #header {
    .header__wrap {
      flex-direction: column;
      align-items: flex-end;
      gap: 0.625rem;
    }
    .header__socials {
      gap: 0.625rem;
      .header__location {
        width: 1.5rem;
        height: 1.5rem;
        i {
          width: 0.75rem;
        }
      }
    }
    .header__social {
      max-width: 1.5rem;
    }
  }
}

@media screen and (max-width: 399px) {
  #header {
    .header__inner {
      gap: 1rem;
    }
    .header__logo {
      width: 8.75rem;
    }
    .header__socials {
      gap: 0.625rem;
      .header__location {
        width: 1.75rem;
        height: 1.75rem;
        i {
          width: 0.75rem;
        }
      }
    }
    .header__social {
      max-width: 1.75rem;
    }
    .header__phone {
      font-size: 0.875rem;
    }
    .header__btn {
      span {
        font-size: 0.875rem;
      }
    }
  }
}