html {
  font-size: max(calc(16px * var(--coef_h)), 15px);
}

body {
  font-family: Montserrat, sans-serif;
}

.fixed-body {
  overflow: hidden;
}

.wrapper {
  position: relative;
  overflow: hidden;
}

.container-fluid {
  max-width: 1920px;
  padding: 0 3.125rem;
}

@media (max-width: 1180px) {
  .container-fluid {
    padding: 0 2.5rem;
  }
}

@media (max-width: 767px) {
  .container-fluid {
    padding: 0 2rem;
  }
}

@media (max-width: 499px) {
  .container-fluid {
    padding: 0 1.5rem;
  }
}

@media (max-width: 359px) {
  .container-fluid {
    padding: 0 1rem;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: PlayfairDisplay;
  font-weight: 600;
}

img {
  object-fit: cover;
}