.footer {
  background: #ffffff;
}
.footer-top {
  border-top: 0.0625rem solid rgba(0, 0, 0, 0.2);
  border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.2);
  padding: 1.125rem 0;
  &__inner {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2.5rem;
  }
  &__left {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
  }
  &__right {
    display: flex;
    align-items: center;
    gap: 11.25rem;
  }
  &__logo {
    width: 11.4375rem;
    img {
      width: 100%;
    }
  }
  &__text {
    font-size: 1rem;
    line-height: 130%;
  }
  &__wrap {
    display: flex;
    gap: 2rem;
  }
  &__email,
  &__location {
    display: flex;
    gap: 1.125rem;
    &:hover {
      background: linear-gradient(2.53deg, rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(199, 162, 97),rgb(164, 123, 62),rgb(137, 92, 35),rgb(117, 70, 15),rgb(105, 57, 3),rgb(102, 53, 0),rgb(106, 58, 4),rgb(121, 74, 19),rgb(144, 101, 42),rgb(176, 137, 74),rgb(208, 173, 106),rgb(236, 213, 166),rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(132, 87, 30),rgb(102, 53, 0));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
      svg path {
        fill: rgb(214, 182, 120);
      }
    }
    i {
      max-width: 1.25rem;
      flex-shrink: 0;
      svg {
        width: 100%;
        height: auto;
      }
    }
    p {
      font-size: 0.875rem;
      line-height: 130%;
      margin: 0;
    }
    strong {
      display: block;
      font-weight: 600;
    }
  }
  &__wrap2 {
    position: relative;
    flex-shrink: 0;
    display: flex;
    gap: 2.5rem;
  }
  &__socials {
    display: flex;
    gap: 1rem;
    .header__location {
      display: none;
    }
  }
  &__social {
    max-width: 2rem;
    &:hover {
      opacity: 0.8;
    }
  }
  &__phone {
    display: block;
    font-size: 1rem;
    line-height: 130%;
    font-weight: 700;
    color: rgb(37, 40, 42) !important;
    &:hover {
      background: linear-gradient(2.53deg, rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(199, 162, 97),rgb(164, 123, 62),rgb(137, 92, 35),rgb(117, 70, 15),rgb(105, 57, 3),rgb(102, 53, 0),rgb(106, 58, 4),rgb(121, 74, 19),rgb(144, 101, 42),rgb(176, 137, 74),rgb(208, 173, 106),rgb(236, 213, 166),rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(132, 87, 30),rgb(102, 53, 0));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
  }
  &__btn {
    background: transparent;
    &:hover {
      span {
        background: linear-gradient(2.53deg, rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(199, 162, 97),rgb(164, 123, 62),rgb(137, 92, 35),rgb(117, 70, 15),rgb(105, 57, 3),rgb(102, 53, 0),rgb(106, 58, 4),rgb(121, 74, 19),rgb(144, 101, 42),rgb(176, 137, 74),rgb(208, 173, 106),rgb(236, 213, 166),rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(132, 87, 30),rgb(102, 53, 0));
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        border-color: rgb(242, 221, 178);
      }
    }
    span {
      min-width: unset;
      padding: 0;
      font-size: 0.875rem;
      line-height: 130%;
      font-weight: 600;
      color: rgb(111, 155, 164);
      border-bottom: 0.0625rem dashed rgb(111, 155, 164);
    }
  }
}
.footer-bottom {
  position: relative;
  z-index: 1;
  background: #ffffff;
  font-size: 0.75rem;
  line-height: 130%;
  font-weight: 500;
  color: rgb(37, 40, 42);
  padding: 1.125rem 0;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem 2rem;
  }
  .b2b {
    width: 13.875rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__center {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem 7.5rem;
  }
  &__text {
    display: flex;
    align-items: center;
    gap: 0.9375rem;
    p {
      margin: 0;
    }
  }
  &__politics {
    &:hover {
      background: linear-gradient(2.53deg, rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(199, 162, 97),rgb(164, 123, 62),rgb(137, 92, 35),rgb(117, 70, 15),rgb(105, 57, 3),rgb(102, 53, 0),rgb(106, 58, 4),rgb(121, 74, 19),rgb(144, 101, 42),rgb(176, 137, 74),rgb(208, 173, 106),rgb(236, 213, 166),rgb(253, 237, 201),rgb(242, 221, 178),rgb(214, 182, 120),rgb(208, 173, 106),rgb(132, 87, 30),rgb(102, 53, 0));
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
  }
}
.footer2 {
  position: absolute !important;
  z-index: 5 !important;
  bottom: 0;
  left: 0;
  width: 100%;
  background: transparent;
  color: #ffffff;
  .footer-bottom {
    background: transparent;
    color: #ffffff;
  }
  .footer-top__inner {
    &:after,
    &:before {
      display: none;
    }
  }
  .footer-top__email,
  .footer-top__location {
    &:hover {
      background: unset !important;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: unset;
      background-clip: unset;
      color: rgb(229, 225, 218);
      svg path {
        fill: rgb(229, 225, 218);
      }
    }
  }
  .footer-top__phone {
    color: rgb(229, 225, 218) !important;
    &:hover {
      background: unset !important;
      -webkit-background-clip: unset;
      -webkit-text-fill-color: unset;
      background-clip: unset;
      color: rgb(255, 255, 255) !important;
    }
  }
  .footer-top__btn {
    &:hover {
      span {
        background: unset !important;
        -webkit-background-clip: unset;
        -webkit-text-fill-color: unset;
        background-clip: unset;
        color: rgb(229, 225, 218) !important;
        border-color: rgb(229, 225, 218);
      }
    }
    span {
      color: #ffffff;
      border-bottom: 0.0625rem dashed #ffffff;
    }
  }
}

@media screen and (max-width: 1850px) {
  .footer {
    position: relative;
    z-index: 2;
  }
  .footer-top {
    &__inner {
      &:after,
      &:before {
        display: none;
      }
    }
  }
}

@media screen and (max-width: 1600px) {
  .footer-top {
    &__inner {
      gap: 2rem;
    }
    &__right {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    &__right {
      align-items: start;
      gap: 3.125rem;
    }
    &__wrap,
    &__wrap2 {
      flex-direction: column;
      gap: 1.5rem;
    }
    &__text {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    &__inner {
      gap: 2rem;
    }
    &__right {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__right {
      flex-direction: column;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-bottom {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__center {
      flex-direction: column;
      align-items: start;
    }
    &__text {
      flex-direction: column;
      align-items: start;
    }
  }
}
